@font-face {
  font-family: 'Gilroy 100';
  src: url('../fonts/Gilroy-Light.eot'); /* IE9 Compat Modes */
  src: url('../fonts/Gilroy-Light.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/Gilroy-Light.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../fonts/Gilroy-Light.woff') format('woff'),
    /* Pretty Modern Browsers */ url('../fonts/Gilroy-Light.ttf') format('truetype'),
    /* Safari, Android, iOS */ url('../fonts/Gilroy-Light.svg#svgFontName') format('svg'); /* Legacy iOS */
}

@font-face {
  font-family: 'Gilroy 900';
  src: url('../fonts/Gilroy-ExtraBold.eot'); /* IE9 Compat Modes */
  src: url('../fonts/Gilroy-ExtraBold.eot?#iefix') format('embedded-opentype'),
    /* IE6-IE8 */ url('../fonts/Gilroy-ExtraBold.woff2') format('woff2'),
    /* Super Modern Browsers */ url('../fonts/Gilroy-ExtraBold.woff') format('woff'),
    /* Pretty Modern Browsers */ url('../fonts/Gilroy-ExtraBold.ttf') format('truetype'),
    /* Safari, Android, iOS */ url('../fonts/Gilroy-ExtraBold.svg#svgFontName') format('svg'); /* Legacy iOS */
}
